<template>
    <p>Joining your scheduled talk</p>
</template>

<script>
    import TalkService from "../../../services/TalkService";
    import EventBus from "../../../services/EventBus";
    import Notify from "../../../services/Notify";

    export default {
        name: "JoinScheduledTalk",
        props: {
            language: String
        },
        async created() {
            try {
                let response = await TalkService.requestTalk(this.language, false)

                if (response.status === 200 && response.data.talkId) {
                    TalkService.setCurrentTalkId(response.data.talkId)
                    EventBus.$emit('new-talk')

                    this.$router.push('/app/talk/' + response.data.talkId)
                    Notify.success('Joined Talk!', "Please wait some time for others to join")
                }
            } catch (e) {
                console.error(e)
                TalkService.clearCurrentTalk()
                EventBus.$emit('new-talk')
                if (e.response.status === 403) {
                    this.$router.push({path: '/sign/in', query: {to: this.$router.currentRoute}})
                }
            }
        }
    }
</script>

