<template>
    <v-card width="100%">
        <v-card-title>
            <h2 class="text-center full-width font-weight-thin">
                Your <span class="brand">Talk</span>s
                <div class="divider"></div>
            </h2>
        </v-card-title>

        <v-card-text>
            <div class="talks" v-if="requests.length > 0">
                <template class="talk-request" v-for="(request, index) in requests">

                    <!-- Language -->
                    <div v-if="request.languageId" v-bind:key="'language-'+index">{{request.languageId}}</div>
                    <div v-else v-bind:key="'language-'+index">{{request.requestedLanguageId}}</div>

                    <!-- Status -->
                    <div class="center" v-bind:key="'status-'+index">
                        <PopupInfo v-bind:key="'status-'+index"
                                   :chip="statusDisplay(request.status).label"
                                   :title="statusDisplay(request.status).label"
                                   :text="statusDisplay(request.status).text"
                                   :icon="statusDisplay(request.status).icon"
                                   :color="statusDisplay(request.status).color">
                        </PopupInfo>
                        <div class="flex-row-center" v-if="request.scheduledAt">
                            <div>
                                <div style="white-space: nowrap">
                                    {{momentInstance(request.scheduledAt).format("LT")}}
                                </div>
                                <div style="white-space: nowrap">
                                    {{momentInstance(request.scheduledAt).format("D MMM (ddd)")}}
                                </div>
                            </div>
                        </div>
                    </div>

                    <!-- Join -->
                    <div v-bind:key="'join-'+index" class="join">

                        <p v-if="request.status === 'NEW'"
                           class="small onhover"><span class="brand">Talk</span> will be here</p>

                        <v-btn v-else-if="request.status === 'SCHEDULED' || request.status === 'JOINED'"
                               @click="joinTalk(request)" fab elevation="5" color="#aad8b0">
                            <div>Join
                                <div class="brand">Talk</div>
                            </div>
                        </v-btn>
                    </div>

                    <!-- More -->
                    <div v-bind:key="'more-'+index">
                        <template v-if="request.status === 'CANCELLED'">
                            <v-btn icon @click="undoCancellation(request)">
                                <v-icon>undo</v-icon>
                            </v-btn>
                        </template>
                        <template v-else>
                            <v-menu bottom left>
                                <template v-slot:activator="{ on }">
                                    <v-btn icon v-on="on">
                                        <v-icon>more_vert</v-icon>
                                    </v-btn>
                                </template>

                                <v-list>
                                    <v-list-item v-if="request.dateTimes"
                                                 @click="dateTimeTableRequest = request">
                                        <v-list-item-icon>
                                            <v-icon>{{mdiCalendarMultiselect}}</v-icon>
                                        </v-list-item-icon>
                                        <v-list-item-title>Show selected date/time</v-list-item-title>
                                    </v-list-item>

                                    <v-list-item v-if="request.status === 'SCHEDULED'"
                                                 @click="addToCalendar(request)">
                                        <v-list-item-icon>
                                            <v-icon>{{mdiCalendarPlus}}</v-icon>
                                        </v-list-item-icon>
                                        <v-list-item-title>Add to calendar</v-list-item-title>
                                    </v-list-item>

                                    <v-list-item v-if="request.status === 'NEW' || request.status === 'SCHEDULED'"
                                                 @click="cancelRequest(request)">
                                        <v-list-item-icon>
                                            <v-icon>cancel</v-icon>
                                        </v-list-item-icon>
                                        <v-list-item-title>Cancel</v-list-item-title>
                                    </v-list-item>
                                </v-list>
                            </v-menu>
                        </template>
                    </div>

                    <div v-if="(requests.length-1) !== index" v-bind:key="'line-'+index" class="line"></div>
                </template>
            </div>
            <div v-else>
                <i>Nothing has scheduled yet</i>
            </div>
        </v-card-text>
        <DateTimeTable :request="dateTimeTableRequest"
                       @close="dateTimeTableRequest = null">
        </DateTimeTable>
    </v-card>
</template>

<script>
    import moment from "moment";
    import TalkService from "../../../services/TalkService";
    import DateTimeTable from "./DateTimeTable";

    import {mdiCalendarMultiselect, mdiCalendarPlus} from '@mdi/js';
    import EventBus from "../../../services/EventBus";
    import Notify from "../../../services/Notify";
    import UserProfileService from "../../../services/UserProfileService";
    import PopupInfo from "../../common/PopupInfo";

    export default {
        name: "UserTalkList",
        // eslint-disable-next-line vue/no-unused-components
        components: {PopupInfo, DateTimeTable},
        data: () => ({
            requests: [],

            dateTimeTableRequest: null,

            mdiCalendarPlus: mdiCalendarPlus,
            mdiCalendarMultiselect: mdiCalendarMultiselect
        }),
        async created() {
            this.updateUserTalks()
            EventBus.$on('update-user-talks', this.updateUserTalks)
        },
        destroyed() {
            EventBus.$off('update-user-talks', this.updateUserTalks)
        },
        methods: {
            async updateUserTalks() {
                this.requests = await TalkService.getUpcomingScheduledRequests();
            },
            statusDisplay(status) {
                switch (status) {
                    case "NEW":
                        return {
                            label: "Matching",
                            color: null,
                            icon: "search",
                            text: "We are looking peers for your <span class='brand'>Talk</span>. Once we can find someone available at the same date/time, we will send you an email as well as update status here"
                        };
                    case "SCHEDULED":
                        return {
                            label: "Scheduled",
                            color: "primary",
                            icon: "schedule",
                            text: "<span class='brand'>Talk</span> has been scheduled. Please feel free to join at specified date/time"
                        };
                    case "NOT_JOINED":
                        return {
                            label: "Not Joined",
                            icon: "rowing",
                            text: "You didn't joined <span class='brand'>Talk</span> at scheduled date/time. If it's a mistake please let us know"
                        };
                    case "CANCELLED":
                        return {
                            label: "Cancelled",
                            icon: "cancel",
                            text: "Request is cancelled. You can undo this action if it was unwanted"
                        };
                    case "FINISHED":
                        return {
                            label: "Finished",
                            icon: "flag",
                            text: "Conversation has been finished. We hope you had a nice <span class='brand'>Talk</span>. Please let us know if something went wrong"
                        };
                    case "JOINED":
                        return {
                            label: "In Progress",
                            icon: "import_export",
                            text: "<span class='brand'>Talk</span> is in progress!"
                        };
                    case "NOT_FULFILLED":
                        return {
                            label: "Not Fulfilled",
                            icon: "block",
                            text: "We could't find anyone. Please consider to select more date/time slots when scheduling"
                        };
                    default:
                        return {label: status};
                }
            },
            momentInstance(dateTime) {
                return moment(dateTime);
            },
            async cancelRequest(request) {
                await TalkService.cancelScheduleRequest(request.id)
                EventBus.$emit('update-user-talks')
            },
            async undoCancellation(request) {
                await TalkService.undoCancellation(request.id)
                EventBus.$emit('update-user-talks')
            },
            async joinTalk(request) {
                try {
                    let talkId = request.talkId;

                    if (!talkId) {
                        let response = await TalkService.requestTalk(request.languageId, false)

                        if (response.status === 200 && response.data.talkId) {
                            talkId = response.data.talkId;
                        }
                    }

                    TalkService.setCurrentTalkId(talkId)
                    EventBus.$emit('new-talk')
                    this.$router.push('/app/talk/' + talkId)
                    Notify.success('Joined Talk!', "Please wait some time for others to join")
                } catch (e) {
                    console.error(e)
                    TalkService.clearCurrentTalk()
                    EventBus.$emit('new-talk')
                    if (e.response.status === 403) {
                        this.$router.push({path: '/sign/in', query: {to: this.$router.currentRoute}})
                    }
                }
            },
            async addToCalendar(request) {
                let userProfile = await UserProfileService.getUserProfile();
                if (userProfile && userProfile.email && userProfile.email.endsWith("@gmail.com")) {
                    let date = moment(request.scheduledAt).utc()
                    let googleFormat = "YYYYMMDDTHHmmss"

                    const googleCalendarUrl = new URL("https://www.google.com/calendar/event?action=TEMPLATE");
                    googleCalendarUrl.searchParams.append("dates", `${date.format(googleFormat)}Z/${date.add(1, 'hour').format(googleFormat)}Z`);
                    googleCalendarUrl.searchParams.append("text", `${request.languageId} Talk`);
                    googleCalendarUrl.searchParams.append("location", `https://app.timetotalk.io/#/app/talk/scheduled/${request.languageId}`);
                    googleCalendarUrl.searchParams.append("details", `Time to Talk would like to invite you to join ${request.languageId} conversation. Have fun!`);

                    window.open(googleCalendarUrl.href, '_blank')
                } else {
                    window.open('http://localhost:8081/api/public/scheduled/calendar.ics?r=' + request.id, '_blank')
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .talks {
        width: 100%;
        height: 100%;
        margin: 15px 0;

        display: grid;
        grid-gap: 5px;
        grid-row-gap: 15px;
        grid-template-columns: min-content 1fr 1fr min-content;
        align-items: center;

        .line {
            grid-column: 1 / -1;
            border-bottom: 1px solid;
            border-color: #dadce0;
        }

        .join {
            place-self: center;
        }

        .center {
            place-self: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }

    button {
        text-transform: initial;
    }
</style>
