<template>
    <div class="status">
        <v-icon v-if="ready" color="#52bb45">cloud_queue</v-icon>
        <v-icon v-else>cloud_off</v-icon>
    </div>
</template>

<script>
    import WebSocketService from "../../../services/WebSocketService";

    export default {
        name: "WsStatus",
        data: () => ({
            ready: false,
            interval: null,
        }),
        created() {
            this.interval = setInterval(() => {
                this.ready = WebSocketService.isReady()
            }, 1500)
        },
        destroyed() {
            clearInterval(this.interval)
        }
    }
</script>

