import axios from 'axios'

const API_URL = process.env.VUE_APP_API_URL

window.onerror = function (msg, url, line) {
    var message = "window.onerror msg=" + encodeURIComponent(msg) + '&amp;url=' + encodeURIComponent(url) + "&amp;line=" + line;
    axios.post(API_URL + '/api/public/logger', message)
};

export default {
    info(message) {
        console.log(message)
        axios.post(API_URL + '/api/public/logger', message)
    },
    error(message) {
        console.error(message)
        axios.post(API_URL + '/api/public/logger', "ERROR: " + message)
    }
}
