import UserService from "./UserService";

const axios = require('axios');
let defaultOptions = () => {
    return {
        headers: {
            'X-User-Token': UserService.getUserToken(),
        },
        withCredentials: true
    }
};


const API_URL = process.env.VUE_APP_API_URL

// f(3) -> 0, 1, 2
// function getRandomInt(max) {
//     return Math.floor(Math.random() * Math.floor(max));
// }
let talk = null;

export default {
    requestTalk(languageId, participateAsNative) {
        return axios.post(API_URL + '/api/private/talk/queue',
            {
                languageId: languageId,
                participateAsNative: participateAsNative
            },
            defaultOptions())
    },

    scheduleTalk(languageId, participateAsNative, dateTimes, userTimeZoneOffset) {
        return axios.post(API_URL + '/api/private/talk/schedule',
            {
                languageId: languageId,
                participateAsNative: participateAsNative,
                dateTimes: dateTimes,
                userTimeZoneOffset: userTimeZoneOffset
            },
            defaultOptions())
    },

    cancelScheduledWithToken(token) {
        axios.post(API_URL + '/api/public/scheduled/cancel',
            {
                token: token
            })
    },

    async cancelScheduleRequest(id) {
        await axios.post(API_URL + '/api/private/talk/schedule/cancel',
            {
                id: id
            },
            defaultOptions())
    },

    async undoCancellation(id) {
        await axios.post(API_URL + '/api/private/talk/schedule/cancel/undo',
            {
                id: id
            },
            defaultOptions())
    },

    getHighDemandDates(languageId, startDate, endDate) {
        return axios.post(API_URL + '/api/private/talk/schedule/available',
            {
                languageId: languageId,
                startDate: startDate,
                endDate: endDate
            },
            defaultOptions())
    },

    async getUpcomingScheduledRequests() {
        let response = await axios.get(API_URL + '/api/private/talk/schedule/requests', defaultOptions())

        if (response.status === 200) {
            if (response.data) {
                return response.data;
            }
        }

        throw new Error(response)
    },

    setCurrentTalkId(talkId) {
        talk = talkId;
    },

    getCurrentTalkId() {
        return talk;
    },

    async getTalkInfo(talkId) {
        let response = await axios.get(API_URL + '/api/private/talk/' + talkId, defaultOptions())

        if (response.status === 200) {
            if (response.data) {
                return response.data;
            }
        }

        throw new Error("user profile retrieval issue")
    },

    clearCurrentTalk() {
        talk = null
        localStorage.removeItem('talk.info')
        localStorage.removeItem('talk.info.expiration')
    },
}
