<template>
    <v-card class="card">
        <p>Your <span class="brand">Talk</span> has been cancelled</p>
        <p>Thanks a lot for letting us know!</p>
        <br>
        <p>To start a new one, go to <a class="brand" href="https://app.timetotalk.io">Time to Talk</a></p>
    </v-card>
</template>

<script>
    import TalkService from "../../../services/TalkService";

    export default {
        name: "CancelScheduled",
        created() {

            let token = this.$route.query.token;

            if (token) {
                TalkService.cancelScheduledWithToken(token)
            }
        }
    }
</script>

