<template>
    <div class="talk-main">

        <div class="head">
            <WsStatus></WsStatus>
            <h2 class="ml-2">{{language}}</h2>
        </div>

        <div class="media">
            <Media :talkId="talkId" :language="language"></Media>
        </div>

        <div class="chat">
            <ChatWrapper></ChatWrapper>
        </div>
    </div>
</template>

<script>
    import Media from "./Media";
    import ChatWrapper from "./chat/ChatWrapper";
    import EventBus from "../../../services/EventBus";
    import WebSocketService from "../../../services/WebSocketService";
    import Notify from "../../../services/Notify";
    import WsStatus from "./WsStatus";
    import LoggerService from "../../../services/LoggerService";
    import TalkService from "../../../services/TalkService";

    export default {
        name: "TalkMain",
        props: {
            talkId: String,
        },
        components: {
            WsStatus,
            ChatWrapper,
            Media
        },
        data: () => ({
            // reconnectRequired: true,
            // connectionAttempts: 0
            language: null,
        }),
        async created() {
            EventBus.$on('ws-opened', this.wsOpened)
            EventBus.$on('ws-error', this.reconnectWS)
            EventBus.$on('ws-closed', this.reconnectWS)
            WebSocketService.openWS(this.talkId);

            let talkInfo = await TalkService.getTalkInfo(this.talkId)
            if (talkInfo) {
                this.language = talkInfo.language
            }

            let clientInfo = {
                appCodeName: navigator.appCodeName,
                appName: navigator.appName,
                appVersion: navigator.appVersion,
                cookieEnabled: navigator.cookieEnabled,
                language: navigator.language,
                onLine: navigator.onLine,
                platform: navigator.platform,
                userAgent: navigator.userAgent
            }

            LoggerService.info("Client info: " + this.fakeUserId + JSON.stringify(clientInfo))
        },
        destroyed() {
            EventBus.$off('ws-opened', this.wsOpened)
            EventBus.$off('ws-error', this.reconnectWS)
            EventBus.$off('ws-closed', this.reconnectWS)
            WebSocketService.send({type: 'LEAVE'})
            WebSocketService.closeWS();
        },
        methods: {
            wsOpened() {
                // this.connectionAttempts = 0;
            },

            async reconnectWS(e) {
                if (e.code === 4404) {
                    Notify.alert('', "Looks like talk wasn't found. Please join another one.")
                    this.$router.push('/')
                } else if (e.code === 4403) {
                    Notify.alert('', "You do not have access to this talk. Please join another one.")
                    this.$router.push('/')
                }
                // else if (this.reconnectRequired && this.connectionAttempts < 5) {
                //     this.connectionAttempts++
                //
                //     await new Promise(r => setTimeout(r, this.connectionAttempts * 1000))
                //
                //     WebSocketService.openWS(this.talkId);
                // }
            },
        }
    }
</script>

<style lang="scss" scoped>
