        height: 85vh;
        max-height: 85vh;
        display: grid;
        grid-gap: 10px;
        grid-template-columns: 1fr 60px;
        grid-template-rows: 1.5rem 1fr;
        grid-template-areas: "head chat" "media chat";
        place-items: stretch;

        .head {
            grid-area: head;
            display: flex;
            align-items: center;
        }

        .media {
            grid-area: media;
        }

        .chat {
            grid-area: chat;
            width: 100%;
            align-self: stretch;
            justify-self: end;
        }

        .section {
            margin: 2%;

            hr {
                width: 50%;
            }

            ol {
                margin-bottom: 1rem;
            }
        }
    }

    @media only screen and (min-width: 1500px) {
        .talk-main {
            grid-template-columns: minmax(1200px, 1fr) minmax(min-content, 500px);
        }
    }
</style>
