<template>
    <div class="self">
        <video id="video-self" autoplay playsinline muted></video>
        <div class="layer-grid">
            <v-btn class="mute-video no-focus" @click="muteVideo" color="secondary" fab>
                <v-icon v-if="videoEnabled">videocam</v-icon>
                <v-icon v-else>videocam_off</v-icon>
            </v-btn>
            <v-btn class="mute-audio no-focus" @click="muteAudio" color="secondary" fab>
                <v-icon v-if="audioEnabled">mic</v-icon>
                <v-icon v-else>mic_off</v-icon>
            </v-btn>
            
<!--            <div class="name">{{remoteUser.name || "Vlad"}}</div>-->
        </div>
    </div>
</template>

<script>
    export default {
        name: "LocalUser",
        props: {
            mediaStream: MediaStream
        },
        data: () => ({
            videoEnabled: true,
            audioEnabled: true
        }),

        mounted() {
            let video = document.querySelector("#video-self")
            video.srcObject = this.mediaStream
        },

        methods: {
            muteVideo() {
                this.videoEnabled = !this.videoEnabled
                this.mediaStream.getVideoTracks().forEach(t => t.enabled = this.videoEnabled)
            },

            muteAudio() {
                this.audioEnabled = !this.audioEnabled
                this.mediaStream.getAudioTracks().forEach(t => t.enabled = this.audioEnabled)
            },
        }
    }
</script>

