<template>
    <div class="card">
        <h1 class="brand"><a href="https://timetotalk.io">Time to Talk</a></h1>
        <div>
            <img src="@/assets/sign-in.png">
        </div>
        <EarlyAccess></EarlyAccess>
        <v-form ref="form" class="full-width" @submit.prevent="signIn">
            <v-text-field ref="email" label="Email" v-model="email" :rules="[InputRules.required, InputRules.email]"
                          prepend-inner-icon="email" type="text"></v-text-field>
            <v-text-field label="Password" v-model="password" :rules="[InputRules.required]"
                          prepend-inner-icon="security" type="password"></v-text-field>

            <v-btn type="submit" :disabled="loading" :loading="loading" block color="primary">Log in</v-btn>
        </v-form>

        <div class="options">
            <p>Not a member? <span><router-link to="/sign/up">Sign up</router-link></span> for free now</p>

            <p>
                <router-link to="/sign/reset">Forgot password?</router-link>
            </p>
        </div>
    </div>
</template>

<script>
    import UserService from '../../services/UserService'
    import Notify from '../../services/Notify'
    import InputRules from "../../services/InputRules"
    import EarlyAccess from "../app/EarlyAccess";

    export default {
        name: "SignIn",
        components: {EarlyAccess},
        data: () => ({
            loading: false,

            email: null,
            password: null,

            InputRules
        }),
        created() {
            const that = this;
            this.$nextTick(() => {
                that.email = (that.$route.query.email) ? that.$route.query.email : null
                that.$refs.email.focus()
            })
        },
        methods: {
            async signIn() {
                if (!this.loading && this.$refs.form.validate()) {
                    try {
                        this.loading = true
                        await UserService.singIn(this.email, this.password);
                        let to = this.$route.query.to
                        if (to) {
                            this.$router.push(to)
                        } else {
                            this.$router.push('/')
                        }
                    } catch (error) {
                        let response = error.response;
                        if (response && response.status === 403 && response.data.reason === 'not validated') {
                            this.$router.push('/sign/verify?email=' + this.email)
                            Notify.info('Almost there', 'Looks like you haven\'t yet verified your email. Please check you mailbox or resend if necessary.')
                        } else if (response && response.status === 403 && response.data.reason === 'not active') {
                            Notify.info('You are all set!', 'You account has not yet activated by our Team. Please be patient while we are providing access.')
                        } else if (response && (response.status === 404 || response.status === 403)) {
                            Notify.alert('Try again', 'Sorry, this does not match our records. Check your spelling and try again.')
                        } else {
                            Notify.handleErrors(error)
                        }
                    } finally {
                        this.loading = false
                    }
                }
            }
        }
    }
</script>

