<template>
    <div class="card">
        <h1 class="brand"><a href="https://timetotalk.io">Time to Talk</a></h1>
        <h3>Reset password</h3>
        <v-form ref="form" class="full-width" @submit.prevent="resetPassword">
            <v-text-field ref="email" label="Email" v-model="email" :rules="[InputRules.required, InputRules.email]"
                          prepend-inner-icon="email" type="text"></v-text-field>

            <v-btn type="submit" :disabled="loading" :loading="loading"
                   block color="primary">Reset password
            </v-btn>
        </v-form>

        <div class="options">
            <p>Not a member? <span><router-link to="/sign/up">Sign up</router-link></span> for free now</p>
        </div>
    </div>
</template>

<script>
    import UserService from "../../services/UserService";
    import Notify from '../../services/Notify'
    import InputRules from "../../services/InputRules";

    export default {
        name: "ResetPassword",
        data: () => ({
            loading: false,

            email: null,
            InputRules
        }),
        created() {
            const that = this;
            this.$nextTick(() => {
                that.email = (that.$route.query.email) ? that.$route.query.email : null
                that.$refs.email.focus()
            })
        }, methods: {
            async resetPassword() {
                if (!this.loading && this.$refs.form.validate()) {
                    try {
                        this.loading = true
                        let response = await UserService.resetPassword(this.email);
                        if (response.status === 200) {
                            this.$router.push('/sign/verify?email=' + this.email)
                            Notify.success("Done", "We've sent email with new validation code/link. Please check your mail box and set new password.")
                        }
                    } catch (error) {
                        let response = error.response;
                        if (response.status === 404) {
                            Notify.alert("Email is not registered", "Please double check email or sign up")
                        } else {
                            Notify.handleErrors(error)
                        }
                    } finally {
                        this.loading = false
                    }
                }
            }
        }
    }
</script>

