<template>
    <div class="media">
        <div :class="'users-grid ' + 'users-grid-for-' + (remoteUsers.length + 1)">
            <LocalUser v-if="mediaStream" :mediaStream="mediaStream"></LocalUser>

            <div v-if="remoteUsers.length === 0" class="users-placeholder">
                <h3>Waiting others</h3>
                <h5>Your peers have {{formatSeconds(countDown)}} to connect</h5>
                <p class="mt-5">Nobody available online right now? Try to</p>
                <v-btn class="green-button" @click="scheduleTalkDialog = true">
                    <span>Schedule <span class="brand">Talk</span></span>
                </v-btn>
                <v-dialog v-model="scheduleTalkDialog" persistent max-width="1024">
                    <ScheduleTalk v-if="scheduleTalkDialog" :language-pre="language"
                                  @finishScheduling="scheduleTalkDialog = false"></ScheduleTalk>
                </v-dialog>
                <!--                <Snake></Snake>-->
            </div>

            <RemoteUser v-bind:key="remoteUser.id" v-for="remoteUser in remoteUsers"
                        @disconnected="remoteDisconnected"
                        :mediaStream="mediaStream"
                        :localUser="localUser" :remoteUser="remoteUser">
            </RemoteUser>
        </div>
    </div>
</template>

<script>
    import LoggerService from "../../../services/LoggerService";
    import Notify from "../../../services/Notify";
    import LocalUser from "./LocalUser";
    import RemoteUser from "./RemoteUser";
    import ScheduleTalk from "../initiate/ScheduleTalkStepper";
    import EventBus from "../../../services/EventBus";
    import WebSocketService from "../../../services/WebSocketService";

    export default {
        name: "Media",
        components: {ScheduleTalk, RemoteUser, LocalUser},
        props: {
            language: String
        },
        data: () => ({
            fakeUserId: null,
            countDown: 599,
            countDownInterval: null,

            mediaStream: null,
            localUser: null,
            remoteUsers: [],

            fullScreen: false,
            connectionEstablished: false,
            reconnectRequired: true,
            connectionAttempts: 0,
            
            scheduleTalkDialog: false
        }),

        mounted() {
            this.fakeUserId = this.$route.query.user || ("fake-user-" + Math.floor(Math.random() * 10000))

            window.remoteUsers = this.remoteUsers //debug only

            LoggerService.info(`webrtc-adapter: ${JSON.stringify(window.adapter.browserDetails)}`)

            this.initiateVideoStream()
            this.startCountDown()
        },
        //"JOINED", "OFFER", "ANSWER", "CANDIDATE", "LEFT",
        created() {
            EventBus.$on('ws-message-JOINED', this.joinedMessage)
            EventBus.$on('ws-message-OFFER', this.offerMessage)
            EventBus.$on('ws-message-ANSWER', this.answerMessage)
            EventBus.$on('ws-message-CANDIDATE', this.candidateMessage)
            EventBus.$on('ws-message-LEFT', this.leftMessage)
        },
        destroyed() {
            EventBus.$off('ws-message-JOINED', this.joinedMessage)
            EventBus.$off('ws-message-OFFER', this.offerMessage)
            EventBus.$off('ws-message-ANSWER', this.answerMessage)
            EventBus.$off('ws-message-CANDIDATE', this.candidateMessage)
            EventBus.$off('ws-message-LEFT', this.leftMessage)
            this.reconnectRequired = false

            if (this.mediaStream != null) {
                this.mediaStream.getTracks().forEach(track => track.stop())
            }
            this.stopCountDown()
        },

        watch: {
            remoteUsers() {
                if (this.remoteUsers.length === 0) {
                    this.startCountDown()
                } else {
                    this.stopCountDown()
                }
            }
        },

        methods: {
            async initiateVideoStream() {
                try {
                    if (!navigator.getUserMedia) {
                        navigator.getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
                    }

                    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
                        this.mediaStream = await navigator.mediaDevices.getUserMedia({
                            video: true,
                            audio: true,
                        })

                        WebSocketService.send({type: 'JOIN'})
                    }
                } catch (e) {
                    LoggerService.error("camera permission declined")
                    Notify.alert('No access to camera', "Please grant us access to your camera for video chat")
                }
            },

            joinedMessage(message) {
                // initializing local user information
                this.localUser = {id: message.user};

                message.users.forEach((remoteUserId) => {
                        this.addRemoteUser({
                            id: remoteUserId
                        })
                    }
                )
            },

            offerMessage(message) {
                this.addRemoteUser({
                    id: message.from,
                    offer: message.payload
                })
            },

            async answerMessage(message) {
                let remoteUser = this.findRemoteUserById(message.from)
                await remoteUser.pc.setRemoteDescription(new RTCSessionDescription(message.payload))
                await remoteUser.addIceCandidates(remoteUser);
            },

            async candidateMessage(message) {
                let remoteUser = this.findRemoteUserById(message.from)
                remoteUser.iceCandidates.push(message)

                if (remoteUser.pc && remoteUser.pc.remoteDescription && remoteUser.pc.remoteDescription.type) {
                    await remoteUser.addIceCandidates(remoteUser);
                }
            },

            leftMessage(message) {
                this.removeRemoteUser(message.user)
            },

            async addIceCandidates(remoteUser) {
                while (remoteUser.iceCandidates && remoteUser.iceCandidates.length > 0) {
                    let message = remoteUser.iceCandidates.shift();
                    try {
                        const candidate = new RTCIceCandidate({
                            sdpMid: message.payload.sdpMid,
                            sdpMLineIndex: message.payload.sdpMLineIndex,
                            candidate: message.payload.candidate,
                        });
                        await remoteUser.pc.addIceCandidate(candidate);
                    } catch (e) {
                        LoggerService.error("Adding candidate issue: " + e + " " + JSON.stringify(message))
                    }
                }
            },

            async remoteDisconnected(remoteUserId) {
                LoggerService.error("Remote user was disconnected localUser " + this.localUser.id + " remote " + remoteUserId)

                let isRemoteUserSentOfferToLocalUser = this.findRemoteUserById(remoteUserId).hasOwnProperty('offer')

                this.removeRemoteUser(remoteUserId)

                if (this.reconnectRequired && !isRemoteUserSentOfferToLocalUser) {
                    LoggerService.info("Reconnect is required " + this.localUser.id + " remote " + remoteUserId)
                    this.$nextTick(() => {
                        this.addRemoteUser({
                            id: remoteUserId
                        });
                    });
                } else {
                    LoggerService.info("Reconnect is not required " + this.localUser.id + "condition: " + this.reconnectRequired + " " + isRemoteUserSentOfferToLocalUser)
                }
            },

            findRemoteUserById(id) {
                let index = this.findIndexRemoteUserById(id);

                return (index >= 0) ? this.remoteUsers[index] : null;
            },

            findIndexRemoteUserById(id) {
                return this.remoteUsers.findIndex(remoteUser => remoteUser.id === id)
            },

            addRemoteUser(remoteUser) {
                if (this.findRemoteUserById(remoteUser.id)) {
                    LoggerService.info("Remote user already connected: localUser " + this.localUser + " remote " + JSON.stringify(remoteUser) + " removing...")
                    this.removeRemoteUser(remoteUser.id)
                }

                this.$nextTick(() => {
                    remoteUser.iceCandidates = []
                    remoteUser.addIceCandidates = this.addIceCandidates
                    this.remoteUsers.push(remoteUser)
                    LoggerService.info("Remote user added: localUser " + this.localUser + " remote " + JSON.stringify(remoteUser))
                });
            },

            removeRemoteUsers() {
                this.remoteUsers = []
            },

            removeRemoteUser(remoteUserId) {
                let remoteUser = this.findRemoteUserById(remoteUserId)

                if (remoteUser != null) {
                    let indexOfRemoteUser = this.findIndexRemoteUserById(remoteUser.id)
                    if (indexOfRemoteUser !== -1) {
                        this.remoteUsers.splice(indexOfRemoteUser, 1)
                    }
                }
            },

            toggleFullScreen() {
                if (this.talkId === 'global') {
                    this.fullScreen = !this.fullScreen
                }
            },

            formatSeconds(time) {
                let minutes = parseInt(time / 60, 10);
                let seconds = parseInt(time % 60, 10);

                minutes = minutes < 10 ? minutes : minutes;
                seconds = seconds < 10 ? "0" + seconds : seconds;

                return minutes + ":" + seconds;
            },

            startCountDown() {
                this.stopCountDown();

                this.countDown = 599
                this.countDownInterval = setInterval(() => {
                    if (this.countDown > 0) {
                        this.countDown--
                    }
                }, 1000);
            },

            stopCountDown() {
                if (this.countDownInterval) {
                    clearInterval(this.countDownInterval);
                }
            }
        }
    }
</script>

