import UserService from "./UserService";

const axios = require('axios');
let defaultOptions = () => {
    return {
        headers: {
            'X-User-Token': UserService.getUserToken(),
        },
        withCredentials: true
    }
};

let userProfile = null
let usersPublicProfiles = {}

const API_URL = process.env.VUE_APP_API_URL

export default {
    async updateUserProfile(name, nativeLanguages) {
        userProfile.name = name;
        userProfile.nativeLanguages = nativeLanguages;

        let response = await axios.post(API_URL + '/api/private/user/profile',
            userProfile,
            defaultOptions())

        if (response.status === 200) {
            if (response.data) {
                userProfile = response.data;
                return
            }
        }

        throw Error("user profile updating issue")
    },

    async getUserProfile() {
        if (userProfile) {
            return userProfile;
        }

        let response = await axios.get(API_URL + '/api/private/user/profile', defaultOptions())

        if (response.status === 200) {
            if (response.data) {
                userProfile = response.data;
            }

            return userProfile;
        }

        throw new Error("user profile retrieval issue")
    },

    async isProfileFilled() {
        let profile = await this.getUserProfile();
        return profile && profile.name;
    },

    async getUserPublicProfile(userId) {
        if (userId) {
            if (userId.startsWith("fake-user-")) {
                return userId
            }
            if (usersPublicProfiles[userId]) {
                return usersPublicProfiles[userId]
            }

            let params = {}
            params.userId = userId;

            let response = await axios.get(API_URL + "/api/private/user/profile/public", {
                params,
                ...defaultOptions()
            })

            if (response.status === 200) {
                if (response.data) {
                    usersPublicProfiles[userId] = response.data;
                }

                return usersPublicProfiles[userId];
            }

            throw Error("user public profile retrieval issue")
        }
    },
}
