<template>
    <div class="card">
        <h1 class="brand"><a href="https://timetotalk.io">Time to Talk</a></h1>
        <div>
            <img src="@/assets/sign-in.png">
        </div>
        <EarlyAccess></EarlyAccess>
        <v-form ref="form" @submit.prevent="signUp" class="full-width">
            <v-text-field ref="email" label="Email" v-model="email" :rules="[InputRules.required, InputRules.email]"
                          prepend-inner-icon="email" type="text"></v-text-field>

            <v-btn type="submit" :disabled="loading" :loading="loading" block color="primary">Sign Up</v-btn>
        </v-form>

        <div class="options">
            <p>Already a member? <span><router-link to="/sign/in">Log in</router-link></span></p>
        </div>

    </div>
</template>

<script>
    import UserService from '../../services/UserService'
    import Notify from '../../services/Notify'
    import InputRules from "../../services/InputRules";
    import EarlyAccess from "../app/EarlyAccess";

    export default {
        name: "SignUp",
        components: {EarlyAccess},
        data: () => ({
            loading: false,

            email: null,
            InputRules
        }),
        created() {
            const that = this;
            this.$nextTick(() => {
                that.email = (that.$route.query.email) ? that.$route.query.email : null
                that.$refs.email.focus()
            })
        },
        methods: {
            async signUp() {
                if (!this.loading && this.$refs.form.validate()) {
                    try {
                        this.loading = true
                        let response = await UserService.signUp(this.email);
                        if (response.status === 200) {
                            this.$router.push('/sign/verify?email=' + this.email)
                            Notify.success('Sign up successful', "Please check your email to verify your account.")
                        }
                    } catch (error) {
                        let response = error.response;
                        console.error(response);

                        if (response.status === 406 && response.data.reason === 'Account already exists') {
                            Notify.alert('Invalid Email', 'This email is not available. Choose a different address.')
                        } else {
                            Notify.handleErrors(error)
                        }
                    } finally {
                        this.loading = false
                    }
                }
            }
        }
    }
</script>

