<template>
    <div class="remote-user">
        <video class="video" :id="'video-' + remoteUser.id" autoplay playsinline></video>

        <div class="layer-grid">
            <!--<div class="controls">
                <v-slider
                        v-model="volume"
                        min="0"
                        max="100"
                        vertical
                        append-icon="volume_up"
                        prepend-icon="volume_down"
                        color="secondary"></v-slider>
            </div>-->

            <div v-if="remoteUser.name" class="name">{{remoteUser.name || ""}}</div>

            <div class="add-peer">
                <v-menu bottom
                        origin="center center"
                        transition="scale-transition"
                        :close-on-content-click="false">
                    <template v-slot:activator="{ on }">
                        <v-btn v-on="on" class="no-focus" color="e0e0e0" fab>
                            <v-icon>person_add</v-icon>
                        </v-btn>
                    </template>
                    <v-card max-width="300px">
                        <v-card-text>
                            <h2>Add peer as your friend is coming soon.</h2>
                            <p>You will be able to see when friends are online and create <span
                                    class="brand">Talk</span> with him/her</p>
                            <p>
                                Any other ideas? Please let us know:
                                <a href="mailto:info@timetotalk.io?subject=Time to Talk feedback">info@timetotalk.io</a>
                            </p>
                        </v-card-text>
                    </v-card>
                </v-menu>
            </div>
        </div>
    </div>
</template>

<script>
    import LoggerService from "../../../services/LoggerService";
    import WebSocketService from "../../../services/WebSocketService";

    export default {
        name: "RemoteUser",
        props: {
            localUser: Object,
            remoteUser: Object,
            mediaStream: MediaStream
        },
        data: () => ({
            audioEnabled: true,
            volume: 100
        }),
        created() {
            this.createPeerConnection()
        },
        destroyed() {
            try {
                this.remoteUser.pc.close()
            } catch (e) {
                LoggerService.error("PC closing issue: " + e)
            }
        },
        watch: {
            volume() {
                this.getVideoElement().volume = this.volume / 100
            }
        },
        methods: {
            createPeerConnection() {
                try {
                    const rtcConfig = {
                        iceServers: [
                            {urls: ['stun:stun.l.google.com:19302']},
                            {urls: ['stun:stun1.l.google.com:19302']},
                            {urls: ['stun:stun2.l.google.com:19302']},
                            {urls: ['stun:stun3.l.google.com:19302']},
                            {urls: ['stun:stun4.l.google.com:19302']},
                            {
                                urls: ['turn:api.timetotalk.io:5349?transport=udp',
                                    'turn:api.timetotalk.io:5349?transport=tcp'],
                                username: 'test',
                                credential: 'key'
                            }
                        ]
                    };

                    this.remoteUser.pc = new RTCPeerConnection(rtcConfig);
                    this.remoteUser.pc.ontrack = this.handleRemoteTrack;
                    this.remoteUser.pc.onicecandidate = this.handleIceCandidate;
                    this.remoteUser.pc.oniceconnectionstatechange = this.handleStateChange;

                    this.mediaStream.getTracks().forEach((track) => this.remoteUser.pc.addTrack(track, this.mediaStream));

                    if (this.remoteUser.offer) { //someone initiated the call
                        this.remoteUser.pc.onnegotiationneeded = async () => {
                            try {
                                await this.remoteUser.pc.setRemoteDescription(new RTCSessionDescription(this.remoteUser.offer));
                                await this.remoteUser.addIceCandidates(this.remoteUser);
                                await this.createRTCAnswer()
                            } catch (e) {
                                LoggerService.info("Remote [" + this.remoteUser.id + "] " + "PC on offer ERROR: " + JSON.stringify(e))
                            }
                        }
                    } else { //this user initiate the call
                        this.remoteUser.pc.onnegotiationneeded = this.createRTCOffer
                    }
                } catch (e) {
                    LoggerService.info("Remote [" + this.remoteUser.id + "] " + "PC ERROR: " + JSON.stringify(e))
                }
            },

            async createRTCOffer() {
                const rtcConstraints = {
                    'OfferToReceiveAudio': true,
                    'OfferToReceiveVideo': true
                };

                try {
                    const offer = await this.remoteUser.pc.createOffer(rtcConstraints);
                    await this.remoteUser.pc.setLocalDescription(offer);

                    const message = {
                        type: 'OFFER',
                        from: this.localUser.id,
                        to: this.remoteUser.id,
                        payload: this.remoteUser.pc.localDescription
                    };
                    console.log('Sending:' + message);
                    WebSocketService.send(message)
                } catch (e) {
                    LoggerService.info("Remote [" + this.remoteUser.id + "] " + "PC create offer ERROR: " + JSON.stringify(e))
                    console.log('cannot create offer: ' + e);
                }
            },

            async createRTCAnswer() {
                const rtcConstraints = {
                    'OfferToReceiveAudio': true,
                    'OfferToReceiveVideo': true
                };

                try {
                    const answer = await this.remoteUser.pc.createAnswer(rtcConstraints);
                    await this.remoteUser.pc.setLocalDescription(answer);

                    const message = {
                        type: 'ANSWER',
                        from: this.localUser.id,
                        to: this.remoteUser.id,
                        payload: this.remoteUser.pc.localDescription
                    };
                    console.log('Sending:' + message);
                    WebSocketService.send(message)
                } catch (e) {
                    LoggerService.info("Remote [" + this.remoteUser.id + "] " + "PC create answer ERROR: " + JSON.stringify(e))
                    console.log('cannot create answer: ' + e);
                }
            },

            handleIceCandidate(event) {
                try {
                    if (event.candidate) {
                        LoggerService.info('send iceCandidate event: ' + event + ' for user: ' + this.remoteUser.id);
                        WebSocketService.send({
                            type: 'CANDIDATE',
                            from: this.localUser.id,
                            to: this.remoteUser.id,
                            payload: event.candidate
                        })
                    } else {
                        console.log('end of candidates');
                    }
                } catch (e) {
                    LoggerService.info("Remote [" + this.remoteUser.id + "] " + "Handle ice candidate ERROR: " + JSON.stringify(e))
                }
            },

            handleRemoteTrack(e) {
                try {
                    this.getVideoElement().srcObject = e.streams[0]
                } catch (e) {
                    LoggerService.info("Remote [" + this.remoteUser.id + "] " + "Remote track ERROR: " + JSON.stringify(e))
                }
            },

            handleStateChange() {
                console.log('handleStateChange event:' + this.remoteUser.pc.iceConnectionState);
                let state = this.remoteUser.pc.iceConnectionState;
                if (state === "failed" || state === "disconnected" || state === "closed") {
                    this.$emit('disconnected', this.remoteUser.id)
                }
            },

            muteVideo() {
                this.videoEnabled = !this.videoEnabled
                this.mediaStream.getVideoTracks().forEach(t => t.enabled = this.videoEnabled)
            },

            getVideoElement() {
                return document.querySelector("#video-" + this.remoteUser.id)
            },

            muteAudio() {
                this.audioEnabled = !this.audioEnabled
                this.getVideoElement().muted = !this.audioEnabled;
            },
        }
    }
</script>

<style lang="scss" scoped>

    .remote-user {
        position: relative;

        width: 100%;
        height: 100%;

        video {
            grid-area: video;
            place-self: center;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .layer-grid {
            position: absolute;
            top: 0;
            left: 0;

            width: 100%;
            height: 100%;

            display: grid;
            grid-template-columns: 1fr 56px;
            grid-template-rows: 1fr 56px;
            grid-gap: 5px;
            padding: 5px;
            grid-template-areas: ". controls" "name add-peer";

            .controls {
                grid-area: controls;
                visibility: hidden;
                background: white;
                border-radius: 20px;
            }

            .name {
                grid-area: name;
                align-self: end;
                width: fit-content;
                background: white;
                border-radius: 20px;
                padding: 3px 8px;
            }

            .add-peer {
                grid-area: add-peer;
                visibility: hidden;
            }

            &:hover {
                .controls {
                    visibility: visible;
                }

                .add-peer {
                    visibility: visible;
                }
            }
        }
    }
</style>
