<template>
    <div class="chat-wrapper-main">
        <v-btn class="show-chat ma-2" v-if="newMessages === 0" @click="drawer = true" color="secondary" fab small>
            <v-icon>chat</v-icon>
        </v-btn>
        <v-badge v-else class="show-chat ma-2"
                 color="primary"
                 :content="newMessages"
                 bordered
                 offset-x="15"
                 offset-y="15"
                 @click.native="drawer = true">
            <v-btn @click="drawer = true" color="secondary" fab small>
                <v-icon>chat</v-icon>
            </v-btn>
        </v-badge>

        <div class="chat-wrapper" :class="{'chat-wrapper-collapsed': !drawer}">
            <v-btn class="collapse" @click="drawer = false" color="secondary" fab small>
                <v-icon>chevron_right</v-icon>
            </v-btn>
            <Chat></Chat>
        </div>
    </div>

</template>

<script>
    import Chat from "./Chat";
    import EventBus from "../../../../services/EventBus";

    export default {
        name: "ChatWrapper",
        components: {Chat},
        data: () => ({
            chatDialog: true,
            mini: true,
            drawer: false,
            newMessages: 0,
        }),
        created() {
            EventBus.$on('ws-message-CHAT_MESSAGE', this.newChatMessage)
        },
        destroyed() {
            EventBus.$off('ws-message-CHAT_MESSAGE', this.newChatMessage)
        },
        watch: {
            drawer() {
                if (this.drawer) {
                    this.newMessages = 0
                }
            }
        },
        methods: {
            newChatMessage(data) {
                if (data.messages && !this.drawer) {
                    this.newMessages += data.messages.length
                }
            }
        }
    }
</script>

