<template>
    <v-card>
        <v-card-title>
            <h3 class="text-center full-width" style="word-break: break-word">
                Please select date and time when you are available
            </h3>
            <div class="divider"></div>
        </v-card-title>

        <v-card-text class="pb-2">
            <ul class="mb-3">
                <li>You have to select <b>at least 3</b> date/time slots (but not more than 20)</li>
                <li :class="{'hide': highDemand.length === 0}">
                    <i class="nice material-icons">check_circle_outline</i> means someone else is looking at this time
                </li>
            </ul>
            <div class="dateTimes" @mouseup="endSelection" @mouseleave="endSelection">
                <div class="clearAll">
                    <i @click="clearAll" class="material-icons">delete_forever</i>
                </div>
                <div v-bind:key="'row-' + index" v-for="index in numberOfTimeSlots" class="row-header">
                    <div v-if="index%2 !== 0">
                        <!-- regex to remove minutes which is always 0 -->
                        {{getTimeForIndex(index-1).format('LT').replace(/(:|\.)[0-9]{2}/, '')}}
                    </div>
                </div>
                <template v-for="index in dates.length">
                    <div class="column-header" v-if="(index-1) % (numberOfTimeSlots) === 0"
                         v-bind:key="'column-' + index">
                        {{getDateForIndex((index-1) / numberOfTimeSlots).format("ddd, D MMM")}}
                    </div>
                    <div v-bind:key="index"
                         :class="'dateTime' + (((index-1) % numberOfTimeSlots % 2 === 0) ? ' strip' : '')"
                         :ref="'cell-' + (index-1)"
                         @mousedown="startSelection(index-1)" @mouseenter="selectDateTime(index-1)">
                        <div class="cell-content">
                            {{dates[index-1].format('LT')}}
                        </div>
                        <i v-if="highDemand[index-1]" class="nice material-icons">check_circle_outline</i>
                    </div>
                </template>
            </div>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="mb-3" width="25%" large @click="finishScheduling">
                <span style="color: #333">Cancel</span>
            </v-btn>
            <v-btn class="mb-3" width="50%" large color="#aad8b0"
                   :disabled="selectedSize < minToSelect"
                   @click="scheduleTalk">Done. Go Next
            </v-btn>
            <v-spacer></v-spacer>
        </v-card-actions>
    </v-card>
</template>

<script>
    import moment from "moment";
    import TalkService from "../../../services/TalkService";

    export default {
        name: "SelectAvailableTime",
        props: {
            language: String,
            participateAsNative: Boolean
        },
        data: function () {
            return {
                numberOfDate: 7,
                timeStart: 10,
                timeDelta: 1,
                timeEnd: 23,
                minToSelect: 3,
                maxToSelect: 20,

                select: null,
                selected: new Set(),
                selectedSize: 0,
                highDemand: []
            }
        },
        created() {
            this.loadDemand()
        },
        computed: {
            numberOfTimeSlots() {
                return (this.timeEnd - this.timeStart) / this.timeDelta;
            },
            dates() {
                // console.info("dates computed");
                var dates = [];

                let date = moment().add(1, 'day')

                for (var i = 0; i < this.numberOfDate; i++) {
                    date.startOf('day')

                    for (var time = this.timeStart; time < this.timeEnd; time += this.timeDelta) {
                        date.set("hour", time)
                        dates.push(date.clone());
                    }

                    date = date.add(1, 'day')
                }

                return dates;
            }
        },
        watch: {
            language() {
                this.loadDemand()
            }
        },
        methods: {
            async loadDemand() {
                if (this.language) {
                    try {
                        let response = await TalkService.getHighDemandDates(this.language,
                            this.dates[0].toISOString(), this.dates[this.dates.length - 1].toISOString())

                        if (response.status === 200) {
                            if (response.data) {
                                let newHighDemand = []

                                if (response.data) {
                                    this.dates.forEach((date, index) => {

                                        let demand = response.data[date.utc().format()]
                                        if (demand && demand > 0) {
                                            newHighDemand[index] = true
                                        }
                                    })
                                }
                                this.highDemand = newHighDemand;
                            }
                        }
                    } catch (e) {
                        console.error(e)
                    }
                }
            },
            getTimeForIndex(index) {
                let date = moment().add(1, 'day')
                date.startOf('day')

                date.set("hour", this.timeStart + index * this.timeDelta)
                return date;
            },
            getDateForIndex(index) {
                let date = moment().add(1 + index, 'day')
                date.startOf('day')
                return date;
            },
            startSelection(index) {
                this.select = !this.selected.has(index);
                this.selectDateTime(index)
            },
            endSelection() {
                this.select = null;
            },
            selectDateTime(index) {
                if (this.select != null) {
                    this.selectDeselectDateTime(index, this.select)
                }
            },
            selectDeselectDateTime(index, select) {
                if (select && this.selected.size < this.maxToSelect) {
                    this.selected.add(index)
                    this.$refs['cell-' + index][0].classList.add('selected');
                } else if (!select) {
                    this.selected.delete(index)
                    this.$refs['cell-' + index][0].classList.remove('selected');
                }
                this.selectedSize = this.selected.size;
            },
            clearAll() {
                Array.from(this.selected).forEach(index => this.selectDeselectDateTime(index, false));
                this.select = null;
            },

            finishScheduling() {
                this.$emit('finishScheduling')
            },
            scheduleTalk() {
                let selectedDateTimes = Array.from(this.selected)
                    .map(index => this.dates[index])
                    .map(date => date.toISOString())

                this.$emit('dateTimeSelected', selectedDateTimes)
            }

        }
    }
</script>

<style lang="scss" scoped>
