    $color-table-strip-dark: #f4f4f4;
    $color-placeholder: #999;
    $color-line: #dadce0;

    .dateTimes {
        display: grid;
        grid-template-columns: 1fr repeat(7, 2fr);
        grid-template-rows: 1fr repeat(13, 30px);
        grid-auto-flow: column;
        user-select: none;

        .clearAll {
            display: flex;
            justify-content: flex-end;
        }

        .column-header {
            text-align: center;
        }

        .row-header {
            font-size: 0.7rem;
            line-height: 0.7rem;
            text-align: center;
        }

        .dateTime {
            position: relative;
            background-color: $color-table-strip-light;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            border-right: 1px solid;
            border-image: linear-gradient(to bottom, white, $color-placeholder, white);
            border-image-slice: 1;

            .cell-content {
                transition: 0.2s ease all;
                visibility: hidden;
                font-size: 0.5rem;
                text-align: center;
                line-height: 0.7rem;
                z-index: 20;
            }

            .material-icons {
                position: absolute;
                right: 5px;
                font-size: 1rem;
            }

            &:hover .cell-content {
                visibility: visible;
                font-size: 0.6rem;
            }

            &.selected {
                background: $color-1;

                .cell-content {
                    visibility: visible;
                    font-size: 0.7rem;
                    font-weight: 600;
                }
            }
        }

        .strip {
            background-color: $color-table-strip-dark;
        }
    }

    .nice {
        color: #2ecc40;
        font-size: 1.5rem;
        vertical-align: middle;
    }

    .hide {
        visibility: hidden;
    }

    button {
        text-transform: initial;
    }
</style>
