<template>
    <div class="select-language">
        <v-select prepend-inner-icon="language" :items="languages" label="Language to practice" v-model="language"
                  :rules="[(v) => !!v || 'Language is required']"
        ></v-select>

        <v-switch inset class="small" dense v-model="participateAsNative"
                  color="primary">
            <template v-slot:label>
                <span class="small full-width" v-if="participateAsNative">Include my native language(s)</span>
                <span class="small full-width" v-else>Please consider helping others as native speaker</span>
                <PopupInfo title="Participate as native"
                           text="When selected, you will have a chance to be matched with other peers using your native language(s). 
                           Only if someone is looking conversation in your native languages.
                           This is the heart of <span class='brand'>Time to Talk</span> please consider helping others as native speaker"
                           icon="help">
                </PopupInfo>
            </template>
        </v-switch>
    </div>
</template>

<script>
    import PopupInfo from "../../common/PopupInfo";

    export default {
        name: "SelectLanguage",
        components: {PopupInfo},
        props: {
            languagePre: String,
            participateAsNativePre: {
                type: Boolean,
                default: false
            }
        },
        data: () => ({
            languages: ['English', 'Spanish', 'French', 'German', 'Russian'],

            language: null,
            participateAsNative: true,
        }),
        created() {
            if (this.languagePre) {
                this.language = this.languagePre
                this.participateAsNative = this.participateAsNativePre
            }
        },
        watch: {
            language() {
                this.$emit('language', this.language)
            },
            participateAsNative() {
                this.$emit('participateAsNative', this.participateAsNative)
            }
        }
    }
</script>

<style lang="scss" scoped>
    .select-language {
        .small {
            font-size: 0.8rem;
            line-height: 0.8rem;
        }
    }
</style>
