<template>
    <v-stepper v-model="step" alt-labels>
        <v-stepper-header>
            <v-stepper-step :editable="step > 1" :complete="step > 1" step="1">Language</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step :editable="step > 2" :complete="step > 2" step="2">Date and Time</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3"><span style="white-space: nowrap;">Number of <span
                    class="brand">Talk</span>s</span></v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
            <v-stepper-content step="1">
                <v-card>
                    <v-card-title>
                        <h3 class="text-center full-width" style="word-break: break-word">
                            Please select language
                        </h3>
                        <div class="divider mb-5"></div>
                    </v-card-title>

                    <v-card-text class="flex-center">
                        <v-form ref="languageForm" class="language-form">
                            <SelectLanguage :language-pre="language"
                                            @language="(val) => {this.language = val}"
                                            @participate-as-native="(val) => {this.participateAsNative = val}"></SelectLanguage>
                        </v-form>
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn class="mb-3" width="25%" large @click="$emit('finishScheduling')">
                            <span style="color: #333">Cancel</span>
                        </v-btn>
                        <v-btn class="mb-3" width="50%" large color="#aad8b0"
                               @click="finishStep1">Done. Go next
                        </v-btn>
                        <v-spacer></v-spacer>
                    </v-card-actions>
                </v-card>
            </v-stepper-content>

            <v-stepper-content step="2">
                <v-lazy>
                    <SelectAvailableTime :language="language"
                                         :participate-as-native="participateAsNative"
                                         @finishScheduling="$emit('finishScheduling')"
                                         @dateTimeSelected="finishStep2">
                    </SelectAvailableTime>
                </v-lazy>
            </v-stepper-content>

            <v-stepper-content step="3">
                <v-card>
                    <v-card-title>
                        <h3 class="text-center full-width" style="word-break: break-word">
                            Please select number of <span class="brand">Talk</span>s you'd like to have
                        </h3>
                        <div class="divider mb-5"></div>
                    </v-card-title>

                    <v-card-text class="flex-center">
                        <v-form class="language-form flex-center">
                            <v-radio-group v-model="numberOfTalks" row>
                                <v-radio label="1" :value="1"></v-radio>
                                <v-radio label="2" :value="2"></v-radio>
                                <v-radio label="3" :value="3"></v-radio>
                            </v-radio-group>
                        </v-form>
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn class="mb-3" width="25%" large @click="$emit('finishScheduling')">
                            <span style="color: #333">Cancel</span>
                        </v-btn>
                        <v-btn class="mb-3" width="50%" large color="#aad8b0" @click="scheduleTalks">
                            <span style="color: #333">
                                Schedule <span class="brand">Talk</span><template v-if="numberOfTalks > 1">s</template>
                            </span>
                        </v-btn>
                        <v-spacer></v-spacer>
                    </v-card-actions>
                </v-card>
            </v-stepper-content>
        </v-stepper-items>
    </v-stepper>
</template>

<script>
    import moment from "moment";
    import SelectAvailableTime from "./SelectAvailableTime";
    import SelectLanguage from "./SelectLanguage";
    import TalkService from "../../../services/TalkService";
    import Notify from "../../../services/Notify";
    import EventBus from "../../../services/EventBus";

    export default {
        name: "ScheduleTalk",
        components: {SelectLanguage, SelectAvailableTime},
        props: {
            languagePre: String,
            participateAsNativePre: {
                type: Boolean,
                default: false
            }
        },
        data: () => ({
            language: null,
            participateAsNative: true,

            selectedDateTimeList: [],

            numberOfTalks: 1,

            step: 1,
            loading: false
        }),
        created() {
            if (this.languagePre) {
                this.language = this.languagePre
                this.participateAsNative = this.participateAsNativePre
                this.step = 2
            }
        },
        methods: {
            finishStep1() {
                if (this.$refs.languageForm.validate()) {
                    this.step = 2
                }
            },
            finishStep2(selectedDateTimeList) {
                this.selectedDateTimeList = selectedDateTimeList
                this.step = 3
            },
            async scheduleTalks() {
                this.loading = true;

                try {
                    for (let i = 0; i < this.numberOfTalks; i++) {
                        await TalkService.scheduleTalk(this.language, this.participateAsNative, this.selectedDateTimeList, moment().utcOffset());
                    }
                    EventBus.$emit('update-user-talks')
                    this.$emit('finishScheduling')
                    Notify.success("Done!", "We will send you an email as soon as we find peers you can talk to.")
                } finally {
                    this.loading = false;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>

    .language-form {
        width: 100%;
        max-width: 400px;
    }

    button {
        text-transform: initial;
    }
</style>
