import UserService from "./UserService";
import LoggerService from "./LoggerService";
import EventBus from "./EventBus";

const fakeUserId = "fake-user-" + Math.floor(Math.random() * 10000)
const acceptableMessages = new Set(["JOINED", "OFFER", "ANSWER", "CANDIDATE", "LEFT", "TALK_INFO", "CHAT_MESSAGE"]);

let webSocket;
let waitingQueue = [];

export default {
    openWS(talkId) {
        try {
            let wsUrl = process.env.VUE_APP_WS_URL + "/ws?X-User-Token=" + UserService.getUserToken() + "&talk=" + talkId;
            if (talkId === 'global') {
                wsUrl += "&X-Fake-User-Id=" + fakeUserId
            }

            webSocket = new WebSocket(wsUrl)
            window.webSocket = webSocket //debug only

            webSocket.onopen = () => {
                waitingQueue.forEach(m => this.send(m))
                EventBus.$emit('ws-opened')
            }
            webSocket.onmessage = (input) => {
                try {
                    let message = JSON.parse(input.data);

                    if (acceptableMessages.has(message.type)) {
                        EventBus.$emit('ws-message-' + message.type, message)
                    }
                } catch (e) {
                    LoggerService.info("WS onmessage ERROR: " + e)
                }
            }
            webSocket.onerror = (e) => EventBus.$emit('ws-error', e)
            webSocket.onclose = (e) => EventBus.$emit('ws-closed', e)
        } catch (e) {
            LoggerService.error("WS connection issues: " + e)
        }
    },

    send(message) {
        if (webSocket && webSocket.readyState === 1) {
            webSocket.send(JSON.stringify(message))
        } else {
            waitingQueue.push(message);
        }
    },

    isReady() {
        return webSocket && webSocket.readyState === 1
    },

    closeWS() {
        waitingQueue = [];
        if (webSocket) {
            webSocket.close();
        }
    }
}
