<template>
<div class="flex-center full-width">
    <div class="card">
        <h1 class="text-center full-width font-weight-thin">
            Profile
        </h1>
        <div>
            <Avatar :user-id="userId" size="150"/>
        </div>
        <div class="email">
            <div>{{email}}</div>
        </div>

        <v-form ref="form" class="full-width" @submit.prevent="save">
            <v-text-field label="Name" v-model="name"
                          prepend-inner-icon="person" type="text"
                          hint="Will be shared with your peers during conversation"
                          :rules="[InputRules.required]"></v-text-field>
            <v-select multiple :items="languages" label="Native language(s)" v-model="nativeLanguages"
                      prepend-inner-icon="language"
                      :rules="[InputRules.required]"></v-select>

            <v-btn type="submit" :disabled="loading" block color="primary">Save</v-btn>
        </v-form>
    </div>
</div>
</template>

<script>
    import Avatar from "./Avatar";
    import InputRules from "../../services/InputRules"
    import UserProfileService from "../../services/UserProfileService";
    import UserService from "../../services/UserService";

    export default {
        name: "UserProfile",
        components: {Avatar},
        data: () => ({
            loading: true,

            name: null,
            email: null,
            nativeLanguages: null,
            languages: ['English', 'Spanish', 'Chinese', 'French', 'Italian', 'Russian', 'German', 'Japanese', 'Other'],
            InputRules
        }),
        async created() {
            try {
                let userProfile = await UserProfileService.getUserProfile();

                const that = this;
                this.$nextTick(() => {
                    that.name = (userProfile.name) ? userProfile.name : null
                    that.email = (userProfile.email) ? userProfile.email : null
                    that.nativeLanguages = (userProfile.nativeLanguages) ? userProfile.nativeLanguages : null
                })
            } finally {
                this.loading = false;
            }
        },
        computed: {
            userId() {
                return UserService.getUserId()
            }
        },
        methods: {
            async save() {
                if (!this.loading && this.$refs.form.validate()) {
                    try {
                        this.loading = true
                        await UserProfileService.updateUserProfile(this.name, this.nativeLanguages);

                        let to = this.$route.query.to
                        if (to) {
                            this.$router.push(to)
                        }
                    } catch (e) {
                        console.log(e)
                    } finally {
                        this.loading = false
                    }
                }
            }
        }
    }
</script>

