<template>
    <div class="card">
        <h1 class="brand"><a href="https://timetotalk.io">Time to Talk</a></h1>
        <div class="avatar-content">
            <div v-html="avatar"></div>
            <div class="email">{{email}}</div>
        </div>

        <v-form ref="form" @submit.prevent="validate" class="full-width">
            <v-text-field label="Key from email" v-model="key" :rules="[InputRules.required]"
                          prepend-inner-icon="vpn_key" type="text"></v-text-field>
            <v-text-field label="Password" v-model="password" :rules="[InputRules.required]"
                          prepend-inner-icon="security" type="password"></v-text-field>
            <v-text-field label="Repeat Password" v-model="password2"
                          :rules="[InputRules.required, InputRules.matchPasswords(password)]"
                          prepend-inner-icon="security" type="password"></v-text-field>

            <v-btn type="submit" :disabled="loading" :loading="loading" block color="primary">Set password</v-btn>
        </v-form>


        <div class="options">
            <p><i>If you can't find email, please check your spam folder</i></p>
            <p>Need to resend verification?
                <v-btn @click="resendValidation" :disabled="loadingResend" :loading="loadingResend"
                       outlined small color="primary">Resend
                </v-btn>
            </p>
            <p>Already a member? <span><router-link to="/sign/in">Log in</router-link></span></p>
        </div>

    </div>
</template>

<script>
    import jdenticon from 'jdenticon';
    import UserService from '../../services/UserService'
    import Notify from '../../services/Notify'
    import InputRules from "../../services/InputRules";

    export default {
        name: "VerifyEmail",
        data: () => ({
            loading: false,
            loadingResend: false,

            email: null,
            key: null,
            password: null,
            password2: null,

            InputRules
        }),
        created() {
            const that = this;
            this.$nextTick(() => {
                that.email = (that.$route.query.email) ? that.$route.query.email : null
                that.key = (that.$route.query.key) ? that.$route.query.key : null
            })
        },
        computed: {
            presetKey() {
                return (this.$route.query.key) ? this.$route.query.key : null
            },
            avatar: function () {
                return jdenticon.toSvg(this.email, 100);
            }
        },
        methods: {
            match(value) {
                console.info(this.password)
                return value === this.password || "Passwords don't match"
            },
            async validate() {
                if (!this.loading && this.$refs.form.validate()) {
                    try {
                        this.loading = true
                        let response = await UserService.validateUserAccount(this.email, this.key, this.password)
                        if (response.status === 200) {
                            this.$router.push('/sign/in?email=' + this.email)
                            Notify.success('You are all set!', 'Please log in the system')
                        }
                    } catch (error) {
                        let response = error.response;
                        if (response.status === 400 && response.data.reason === 'Invalid validation key') {
                            Notify.alert('Invalid validation key', 'Please check you mailbox or resend if necessary')
                        } else if (response.status === 400 && response.data.reason === 'Update was not requested') {
                            Notify.info('Not requested', "Password update wasn't requested for this account")
                        } else {
                            Notify.handleErrors(error)
                        }
                    } finally {
                        this.loading = false
                    }
                }
            },
            async resendValidation() {
                if (!this.loadingResend) {
                    try {
                        this.loadingResend = true
                        let response = await UserService.resetPassword(this.email)
                        if (response.status === 200) {
                            Notify.success('Success', "We've sent email with new verification code.")
                        }
                    } catch (error) {
                        let response = error.response;
                        if (response.status === 404) {
                            Notify.alert('Invalid email', 'Email is not registered. Please sign up.')
                        } else {
                            Notify.handleErrors(error)
                        }
                    } finally {
                        this.loadingResend = false
                    }
                }
            }
        }
    }
</script>

