import Vue from "vue";
import VueRouter from 'vue-router';

import AppLayout from '../components/layout/AppLayout';
import CenterCardLayout from '../components/layout/CenterCardLayout';

import UserService from '../services/UserService';

import Home from '../components/app/Home';
import UserProfile from '../components/app/UserProfile';
import SignIn from '../components/a12n/SignIn';
import SignUp from '../components/a12n/SignUp';
import VerifyEmail from '../components/a12n/VerifyEmail';
import ResetAccount from '../components/a12n/ResetAccount';
import TalkMain from '../components/app/talk/TalkMain';
import JoinScheduledTalk from "../components/app/initiate/JoinScheduledTalk";
import CancelScheduled from "../components/app/talk/CancelScheduled";
import UserProfileService from "../services/UserProfileService";


Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/app'
        },
        {
            path: '/app',
            component: AppLayout,
            children: [
                {
                    path: '',
                    component: Home
                },
                {
                    path: 'profile',
                    component: UserProfile
                },
                // {
                //     path: 'profile',
                //     component: CenterCardLayout,
                //     children: [
                //         {
                //             path: '',
                //             component: UserProfile
                //         },
                //     ]
                // },
                {
                    path: 'talk/:talkId',
                    component: TalkMain,
                    props: true
                },
                {
                    path: 'talk/scheduled/:language',
                    component: JoinScheduledTalk,
                    props: true
                }
            ]
        },
        {
            path: "/sole",
            component: CenterCardLayout,
            children: [
                {
                    path: 'profile',
                    component: UserProfile
                },
            ]
        },
        {
            path: "/sign",
            component: CenterCardLayout,
            children: [
                {
                    path: '',
                    redirect: 'in'
                },
                {
                    path: 'in',
                    component: SignIn
                },
                {
                    path: 'up',
                    component: SignUp
                },
                {
                    path: 'verify',
                    component: VerifyEmail
                },
                {
                    path: 'reset',
                    component: ResetAccount
                }
            ]
        },
        {
            path: "/direct",
            component: CenterCardLayout,
            children: [
                {
                    path: 'scheduled/cancel',
                    component: CancelScheduled,
                }
            ]
        }
    ]
})

// const nprogress = require('nprogress')
// nprogress.configure({parent: '#app'})

router.beforeEach(async (to, from, next) => {
    // nprogress.start()
    if (to.path === '/app/talk/global') {
        next()
        return;
    }

    if (to.matched.some(record => record.path.startsWith('/app') || record.path.startsWith('/sole'))) {
        if (await UserService.isAuthenticated()) {
            if (to.matched.some(record => record.path.startsWith('/app'))) {
                let profileFilled = await UserProfileService.isProfileFilled();
                if (profileFilled) {
                    next()
                } else {
                    next({path: '/sole/profile', query: {to: to.path}});
                }
            } else {
                next()
            }
        } else {
            next({path: '/sign/in', query: {to: to.path}});
        }
    } else {
        next()
    }
    // nprogress.set(0.1)
})
router.afterEach(() => {
    // setTimeout(() => nprogress.done(), 500)
})


export default router
