<template>
    <div class="chat">

        <div ref="messages" class="messages">
            <div class="message" v-bind:key="index" v-for="(message, index) in messages">
                <div class="avatar">
                    <Avatar :user-id="message.userId" size="35"></Avatar>
                </div>
                <!--                <div class="name">{{}}</div>-->
                <div class="name">{{message.name}}</div>
                <div class="text">
                    <span v-bind:key="i" v-for="(t, i) in message.text">
                        <template v-if="t.type === 'url'">
                            <a target="_blank" :href="t.value">{{t.value}}</a>
                        </template>
                        <template v-else>
                            {{t.value}}
                        </template>
                    </span>
                </div>
            </div>
        </div>

        <v-text-field autofocus type="text" v-model="text" placeholder="Write your message here" append-icon="send"
                      @keyup.enter="sendMessage" @click:append="sendMessage"></v-text-field>
    </div>
</template>

<script>
    import Avatar from "../../Avatar";
    import UserService from "../../../../services/UserService";
    import EventBus from "../../../../services/EventBus";
    import WebSocketService from "../../../../services/WebSocketService";
    import UserProfileService from "../../../../services/UserProfileService";

    const urlRegExp = "https?:\\/\\/(www\\.)?[-a-zA-Z0-9@:%._\\+~#=]{1,256}\\.[a-zA-Z0-9()]{1,6}\\b([-a-zA-Z0-9()@:%_\\+.~#?&//=]*)"

    export default {
        name: "Chat",
        components: {Avatar},
        data: () => ({
            text: "",
            messages: []
        }),
        created() {
            EventBus.$on('ws-message-CHAT_MESSAGE', this.newChatMessage)
        },
        destroyed() {
            EventBus.$off('ws-message-CHAT_MESSAGE', this.newChatMessage)
        },
        methods: {
            async newChatMessage(data) {
                if (data.messages) {
                    for (const message of data.messages) {
                        await this.displayMessage(message);
                    }
                }
            },
            async sendMessage() {
                if (this.text) {
                    let message = {
                        text: this.text,
                        type: "CHAT_MESSAGE",
                        userId: UserService.getUserId()
                    }

                    WebSocketService.send(message)
                    await this.displayMessage(message)
                    this.text = ""
                }
            },
            async displayMessage(message) {
                if (message) {
                    let text = message.text.trim();

                    if (text.length > 0) {
                        let array = [...text.matchAll(urlRegExp)];

                        let textArray = []
                        let start = 0;

                        if (array.length > 0) {
                            array.forEach(match => {
                                textArray.push({
                                    type: "text",
                                    value: text.slice(start, match.index)
                                })
                                textArray.push({
                                    type: "url",
                                    value: match[0]
                                })
                                start = match.index + match[0].length;
                            })
                        }

                        textArray.push({
                            type: "text",
                            value: text.substr(start, text.length)
                        })

                        let userPublicProfile = await UserProfileService.getUserPublicProfile(message.userId);

                        this.messages.push({
                            text: textArray,
                            userId: message.userId,
                            name: userPublicProfile && userPublicProfile.name ? userPublicProfile.name : "",
                            id: message.id
                        })

                        this.$nextTick(() => {
                            let container = this.$refs.messages;
                            container.scrollTop = this.$refs.messages.scrollHeight;
                        })
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .chat {
        display: grid;
        grid-template-rows: 1fr 3rem;
        width: 100%;
        height: 100%;

        .messages {
            overflow-y: scroll !important;

            .message {
                margin: 25px 0;
                display: grid;
                grid-template-columns: 40px 1fr;
                grid-template-rows: 1rem 1fr;
                grid-template-areas: "avatar name" "avatar text";

                .avatar {
                    grid-area: avatar;
                }

                .name {
                    grid-area: name;
                    font-size: 0.7rem;
                }

                .text {
                    overflow: auto;
                    grid-area: text;
                    font-size: 1rem;

                    a[target="_blank"]::after {
                        content: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAQElEQVR42qXKwQkAIAxDUUdxtO6/RBQkQZvSi8I/pL4BoGw/XPkh4XigPmsUgh0626AjRsgxHTkUThsG2T/sIlzdTsp52kSS1wAAAABJRU5ErkJggg==);
                        margin: 0px 3px 0px 5px;
                    }
                }
            }
        }
    }

</style>
