<template>

    <div class="avatar">
        <div class="avatar-svg" v-html="avatar"></div>
    </div>

</template>

<script>
    import jdenticon from 'jdenticon';

    export default {
        name: "Avatar",
        props: ['size', 'userId'],
        computed: {
            avatar() {
                return this.userId != null ? jdenticon.toSvg(this.hash(), this.size) : "";
            }
        },
        methods: {
            hash() {
                var hash = 0, i, chr;
                for (i = 0; i < this.userId.length; i++) {
                    chr = this.userId.charCodeAt(i);
                    hash = ((hash << 5) - hash) + chr;
                    hash |= 0;
                }
                return hash;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .avatar {
        margin: 3%;
        border-radius: 4px;

        .avatar-svg {
            line-height: 0;
        }
    }
</style>
