<template>
    <v-btn @click="finishTalk" outlined>Finish <span class="brand">Talk</span></v-btn>
</template>

<script>
    import TalkService from "../../../services/TalkService";
    import EventBus from "../../../services/EventBus";

    export default {
        name: "FinishTalk",
        methods: {
            finishTalk() {
                TalkService.clearCurrentTalk();
                EventBus.$emit('new-talk')
                if (this.$router.currentRoute.path !== '/app' && this.$router.currentRoute.path !== '/') {
                    this.$router.push('/')
                }
            }
        }
    }
</script>

