import moment from 'moment'
import axios from 'axios'

const API_URL = process.env.VUE_APP_API_URL

let isAuthenticated = false

export default {
    signUp(email) {
        return axios.post(API_URL + '/api/public/user/sign/up', {email: email})
    },

    _setUserToken(token) {
        localStorage.setItem('user.token', token)
        localStorage.setItem('user.token.expiration', moment().add(1, 'days').format())
    },

    getUserToken() {
        return localStorage.getItem('user.token')
    },

    _setUserId(userId) {
        return localStorage.setItem('user.id', userId)
    },

    getUserId() {
        return localStorage.getItem('user.id')
    },

    async isAuthenticated() {
        let userTokenExpiration = localStorage.getItem('user.token.expiration')
        if (userTokenExpiration == null || moment(userTokenExpiration).isBefore(moment())) {
            this._cleanUserData()
            isAuthenticated = false
        }

        let userToken = this.getUserToken()

        if (!isAuthenticated && userToken && userToken !== '') {
            try {
                let response = await axios.post(API_URL + '/api/private/user/check', {}, {
                    headers: {
                        'X-User-Token': userToken,
                    },
                    withCredentials: true
                });

                isAuthenticated = response.status === 200
            } catch (e) {
                isAuthenticated = false
            }
        }

        return isAuthenticated;
    },

    signOut() {
        this._cleanUserData()
        isAuthenticated = false
        // axios.post(API_URL + '/api/sign/out')
        //send event to server
    },

    _cleanUserData() {
        localStorage.removeItem('user.id')
        localStorage.removeItem('user.info')
        localStorage.removeItem('user.token')
        localStorage.removeItem('user.token.expiration');
    },

    async singIn(login, password) {
        let response = await axios.post(API_URL + '/api/public/user/login',
            {
                login: login,
                password: password
            },
            {
                withCredentials: true
            });

        if (response.status === 200) {
            this._setUserToken(response.data.token)
            this._setUserId(response.data.userId)
            return;
        }

        let error = new Error()
        error.response = response

        throw error
    },

    validateUserAccount(email, key, password) {
        return axios.post(API_URL + '/api/public/user/password',
            {
                email: email,
                key: key,
                password: password
            })
    },

    resetPassword(email) {
        return axios.delete(API_URL + '/api/public/user/password?email=' + email)
    }
}
