<template>
    <v-dialog v-if="request" v-model="showDialog" max-width="1024">
        <v-card>
            <v-card-title>
                Selected Date/Time
                <v-spacer></v-spacer>
                <v-icon @click="$emit('close')">close</v-icon>
            </v-card-title>
            <v-card-text>
                <div class="dateTimes">
                    <div></div>
                    <div v-bind:key="'row-' + index" v-for="index in numberOfTimeSlots" class="row-header">
                        <div v-if="index%2 !== 0">
                            <!-- regex to remove minutes which is always 0 -->
                            {{getTimeForIndex(index-1).format('LT').replace(/(:|\.)[0-9]{2}/, '')}}
                        </div>
                    </div>
                    <template v-for="index in dates.length">
                        <div class="column-header" v-if="(index-1) % (numberOfTimeSlots) === 0"
                             v-bind:key="'column-' + index">
                            {{getDateForIndex((index-1) / numberOfTimeSlots).format("ddd, D MMM")}}
                        </div>
                        <div v-bind:key="index"
                             :class="'dateTime' + (((index-1) % numberOfTimeSlots % 2 === 0) ? ' strip' : '') 
                             + (isPreselected(index-1) ? ' selected' : '')
                             + (isScheduled(index-1) ? ' scheduled' : '')">
                            <div class="cell-content">
                                {{dates[index-1].format('LT')}}
                            </div>
                        </div>
                    </template>
                </div>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
    import moment from "moment";

    export default {
        name: "DateTimeTable",
        props: {
            request: Object
        },
        data: function () {
            return {
                numberOfDate: 7,
                timeStart: 10,
                timeDelta: 1,
                timeEnd: 23,
                minToSelect: 3,
                maxToSelect: 20,

                showDialog: false
            }
        },
        watch: {
            request() {
                this.showDialog = this.request !== null
            }
        },
        computed: {
            numberOfTimeSlots() {
                return (this.timeEnd - this.timeStart) / this.timeDelta;
            },
            dates() {
                let dates = [];

                let date = moment(this.request.createdAt).add(1, 'day')

                for (let i = 0; i < this.numberOfDate; i++) {
                    date.startOf('day')

                    for (let time = this.timeStart; time < this.timeEnd; time += this.timeDelta) {
                        date.set("hour", time)
                        dates.push(date.clone());
                    }

                    date = date.add(1, 'day')
                }

                return dates;
            }
        },
        methods: {
            getTimeForIndex(index) {
                let date = moment(this.request.createdAt).add(1, 'day')
                date.startOf('day')

                date.set("hour", this.timeStart + index * this.timeDelta)
                return date;
            },
            getDateForIndex(index) {
                let date = moment(this.request.createdAt).add(1 + index, 'day')
                date.startOf('day')
                return date;
            },
            isPreselected(index) {
                return this.request.dateTimes.find(dateTime => this.dates[index].isSame(dateTime))
            },
            isScheduled(index) {
                return this.request.scheduledAt && this.dates[index].isSame(this.request.scheduledAt)
            }
        }
    }
</script>

<style lang="scss" scoped>
    $color-table-strip-light: #fff;
    $color-table-strip-dark: #f4f4f4;
    $color-placeholder: #999;
    $color-line: #dadce0;

    .dateTimes {
        display: grid;
        grid-template-columns: 1fr repeat(7, 2fr);
        grid-template-rows: 1fr repeat(13, 30px);
        grid-auto-flow: column;
        user-select: none;

        .column-header {
            text-align: center;
        }

        .row-header {
            font-size: 0.7rem;
            line-height: 0.7rem;
            text-align: center;
        }

        .dateTime {
            position: relative;
            background-color: $color-table-strip-light;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            border-right: 1px solid;
            border-image: linear-gradient(to bottom, white, $color-placeholder, white);
            border-image-slice: 1;

            .cell-content {
                transition: 0.2s ease all;
                visibility: hidden;
                font-size: 0.5rem;
                text-align: center;
                line-height: 0.7rem;
                z-index: 20;
            }

            .material-icons {
                position: absolute;
                right: 5px;
                font-size: 1rem;
            }

            &:hover .cell-content {
                visibility: visible;
                font-size: 0.6rem;
            }

            &.selected {
                background: $color-1;

                .cell-content {
                    visibility: visible;
                    font-size: 0.7rem;
                    font-weight: 600;
                }
            }

            &.scheduled {
                border: 2px solid;
                background-color: #aad8b0;
            }
        }

        .strip {
            background-color: $color-table-strip-dark;
        }
    }

    .nice {
        color: #2ecc40;
        font-size: 1.5rem;
        vertical-align: middle;
    }

    .hide {
        visibility: hidden;
    }

    button {
        text-transform: initial;
    }
</style>
