var impl;

export default {
    init(that) {
        impl = that;
    },

    success(title, text) {
        impl.success(text, {labels: {success: title}});
    },

    warning(title, text) {
        impl.warning(text, {labels: {warning: title}})
    },

    info(title, text) {
        impl.info(text, {labels: {info: title}})
    },

    alert(title, text) {
        impl.alert(text, {labels: {alert: title}})
    },

    handleErrors(error) {
        if (error && error.response && error.response.status === 500) {
            this.alert('Server issue',
                'Something wrong is on our side. We are working on fix, please retry a bit later');
        } else {
            this.alert('Connection error',
                'Looks like our servers can\'t be reached. Please double check your internet connection');
        }
    }

}
