import Vue from 'vue';
import Vuex from 'vuex';
import VuexPersist from 'vuex-persist'

//TODO refactor when store becomes too big. Move it to @/store folder

Vue.use(Vuex)

const vuexPersist = new VuexPersist({
    key: 'ttt-app',
    storage: window.localStorage
})

const talkModule = {
    namespaced: true,
    state: {
        // null - not initiated, LOOKING, READY, IN_PROGRESS
        talkStatus: null,
        talkDetails: null
    },
    mutations: {
        talkStatus(state, value) {
            state.talkStatus = value
        },
        talkDetails(state, value) {
            state.talkDetails = value;
        }
    },
    actions: {},
    modules: {}
}

export default new Vuex.Store({
    modules: {
        talk: talkModule
    },
    plugins: [vuexPersist.plugin]
})
